.home {
    width: 100%;
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: left;
    flex-direction: column;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    background-color: #8fbea4;
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  }
  
  .headerContainer {
    width: auto;
    margin-left: 50px;
  }
  
  .headerContainer h1 {
    justify-content: center;
    align-items: center;
    font-size: 90px;
    height: 60px;
    color: rgb(170, 149, 235);
    text-shadow: 2px 2px 4px #000000;
    
  }
  
  .headerContainer p {
    padding: 0;
    font-size: 40px;
    font-weight: lighter;
    color: white;
    text-shadow: 2px 2px 3px #000000;
  }
  
  .headerContainer button {
    border: none;
    border-radius: 5px;
    cursor: pointer;
    justify-content: center;
    background-color: rgb(80, 47, 47);
    height: 50px;
    width: 180px;
    color: white;
    text-decoration: none;
    font-size: 20px;
  }
  
  .headerContainer button:hover {
    background-color: rgb(80, 47, 47);
    cursor: pointer;
    transition: 0.3s ease-in;
  }
  
  @media only screen and (max-width: 650px) {
    .home {
      justify-content: center;
      align-items: center;
    }
    
  
    .headerContainer {
      margin-top: 90%;
      margin-left: 0px;
      border-radius: 10px;
      padding: 10px;
      display: flex;
      justify-content: center;
      align-items: center;
      opacity: .9;
      background-color: rgb(80, 47, 47);
      color: white;
    }
  
    .headerContainer h1 {
      display: flex;
      justify-content: center;
      align-items: center;
      margin-left: 10px;
      font-size: 50px;
      height: 30px;
      color: white;
    }
  
    .headerContainer p {
      opacity: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 20px;
      color: white;
    }
  
    .headerContainer button {
      
      background-color: white;
      color: rgb(80, 47, 47);
      margin-bottom: 30px;
    }
  
    .headerContainer button:hover {
      background-color: rgb(225, 225, 225);
      color:rgb(80, 47, 47);
    }
  }